// https://docs.atlassian.com/aui/latest/docs/auiselect2.html
// TODO: ui-select with aui theme
// see also https://github.com/angular-ui/ui-select
// and https://bitbucket.org/atlassian/aui/src/master/src/js/aui-select2.js
app.directive('auiSelect2', ['$timeout', function ($timeout) {
    return {
        require: 'ngModel',
        restrict: 'A',
        link: function(scope, elem, attrs, ngModel) {
            scope.$watch(attrs.ngModel, function() {
                $(elem).auiSelect2();
            });
        }
    }
}]);

app.directive('columnsDropdown', ['$timeout', function ($timeout) {
    return {
        restrict: 'E',
        scope: {
            isGadget: "=",
            columns: "=",
            selected: "=",
            onClose: "&"
        },
        templateUrl: '/templates/columnsDropdown.html',
        link: function(scope, elem, attrs) {
            scope.checked = function(column) {
                return scope.selected.indexOf(column.id) >= 0 ? 'checked' : '';
            };

            scope.save = function() {
                var newIds = [];
                var columnItems = angular.element('#columns-dropdown > div > ul > li > .checked');
                columnItems.each(function(index, columnItem) {
                    var $columnItem = angular.element(columnItem);
                    newIds.push($columnItem.scope().column.id);
                });
                updateIds(scope.selected, newIds);
                angular.element('#columns-dropdown-trigger').trigger('aui-button-invoke');
                scope.onClose();
            };

            // https://bitbucket.org/atlassian/aui/src/master/src/js/dropdown2.old.js
            // FIXME: close the menu when clicking on elements which aren't "interactive"
            // see https://bitbucket.org/atlassian/aui/src/master/src/js/dropdown2.js?#cl-295
            scope.cancel = function() {
                var columnItems = angular.element('#columns-dropdown > div > ul > li > a');
                columnItems.each(function(index, columnItem) {
                    var $columnItem = angular.element(columnItem);
                    var columnId = $columnItem.scope().column.id;
                    if (scope.selected.indexOf(columnId) == -1) {
                        $columnItem.removeClass('checked');
                        $columnItem.removeClass('aui-dropdown2-checked');
                    } else {
                        $columnItem.addClass('checked');
                    }
                });
                angular.element('#columns-dropdown-trigger').trigger('aui-button-invoke');
            };

            var updateIds = function(selected, newIds) {
                // remove deselected
                angular.forEach(selected, function(id, index) {
                    if (newIds.indexOf(id) == -1) {
                        selected.splice(index, 1);
                    }
                });
                // add newly selected
                angular.forEach(newIds, function(id) {
                    if (selected.indexOf(id) == -1) {
                        selected.push(id);
                    }
                });
            };
        }
    }
}]);

app.directive('auiIssuePicker', ['searchService', function(searchService) {
    return {
        scope: {
            ngModel: '=',
            ngDisabled: '='
        },
        restrict: 'A',
        link: function(scope, elem, attrs) {
            $(elem).auiSelect2({
                placeholder: "", // using placeholder for selected value, because $(elem).auiSelect2({...}).select2('val', '1'); works ONLY for {multiple: true} @see: http://jsfiddle.net/arunpjohny/VpnCe/3/
                multiple: false,
                ajax: {
                    url: "/rest/api/2/issue/picker",
                    type: "GET",
                    dataType: 'json',
                    cache: true,
                    // query parameters for the remote ajax call
                    data: function data(term) {
                        return {
                            query: term,
                            currentJQL: '',
                            showSubTasks: true
                        };
                    },
                    // parse data from the server into form select2 expects
                    results: function results(data) {
                        data = SearchUtils.getJson(data);
                        var results = [];
                        if (data.sections.length > 0) {
                            results.push({select2Section: data.sections[0].label, children: data.sections[0].issues});
                            if (data.sections.length > 1) {
                                results.push({select2Section: data.sections[1].label, children: data.sections[1].issues});
                            }
                        }
                        return {
                            results: results
                        };
                    },
                    // select2 uses $.ajax as a default transport function so we have to override it
                    // to use AP.request for cross-origin communication
                    transport: function transport(params) {
                        AP.request({
                            url: params.url,
                            headers: {
                                "Accept": "application/json"
                            },
                            data: params.data,
                            success: params.success,
                            error: params.error
                        });
                    }
                },
                // specify id parameter of each issue entity
                id: function id(issue) {
                    return issue.key;
                },
                // define how selected element should look like
                formatSelection: function formatSelection(issue) {
                    return Select2.util.escapeMarkup(issue.key + ': ' + issue.summaryText);
                },
                // define how single option should look like
                formatResult: function formatResult(issue, container, query, escapeMarkup) {
                    if (issue.hasOwnProperty('select2Section')) {
                        return issue.select2Section;
                    }
                    // format result string
                    var resultText = issue.key + ': ' + issue.summaryText;
                    var higlightedMatch = [];
                    // we need this to disable html escaping by select2 as we are doing it on our own
                    var noopEscapeMarkup = function noopEscapeMarkup(s) { return s; };

                    // highlight matches of the query term using matcher provided by the select2 library
                    Select2.util.markMatch(escapeMarkup(resultText), escapeMarkup(query.term), higlightedMatch, noopEscapeMarkup);

                    // convert array to string
                    return higlightedMatch.join("");
                },
                initSelection: function(element, callback) {
                    // assert(element.val() == scope.ngValue.key)
                    if (scope.ngModel) {
                        searchService.findIssueByKey(scope.ngModel).then(function (issue) {
                            callback({
                                key: issue.key,
                                summaryText: issue.fields.summary
                            });
                        });
                    }
                },
                //  define message showed when there are no matches
                formatNoMatches: function formatNoMatches(query) {
                    return "No matches found";
                }
            });
            scope.$watch('ngModel', function(issueKey) {
                if (elem.select2("val") != issueKey) {
                    elem.select2("val", issueKey); // trigger initSelection
                }
            });
        }
    }
}]);

