app.directive('codemirror', [function () {
    return {
        restrict: 'A',
        require: 'ngModel',
        scope: {
            codemirrorOpts: '='
        },
        link: function(scope, elem, attrs, ngModel) {
            var cm = CodeMirror.fromTextArea(elem[0], scope.codemirrorOpts);
            ngModel.$render = function () {
              var safeViewValue = ngModel.$viewValue || '';
              cm.setValue(safeViewValue);
            };
            cm.on('changes', function(instance, changes) {
                var newValue = instance.getValue();
                if (newValue !== ngModel.$viewValue) {
                    if (!scope.$root.$$phase) {
                        scope.$apply(function() {
                          ngModel.$setViewValue(newValue);
                        });
                    } else {
                        ngModel.$setViewValue(newValue);
                    }
                    if (scope.$parent.columnForm) {
                        scope.$parent.columnForm.$setPristine();
                    }
                }
            });
        }
    }
}]);

